/*** defined tags ***/

/*** User classes ***/
.mainContainer{
    font-size: 20px;
    padding: 5px;
    width: 500px;
    height: 500px;
    max-width: auto;
    max-height: auto;
    background-color: whitesmoke;
    margin-left: auto;
    margin-right: auto;

}

.subContainer{
    padding: 5px;
    width: 450px;
    height: 450px;
    background-color: black;
    opacity: 10%;
    margin-left: auto;
    margin-right: auto;
}

.subContainer:hover{   
    padding: 5px;
    width: 450px;
    height: 450px;
    background-color: white;
    margin-left: auto;
    margin-right: auto;
    opacity: 100%;
}
.startButton{
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
}

.Player{
    width: 450px;
}
